<template>
<div class="scrollBox">
  <header-nav v-if="$utils.showNavBar()" :msg="''" :isDetail="1"></header-nav>
  <div id="questionDetail" :class="{hasHeader: $utils.showNavBar()}">
    <div v-if="$route.params.tab < 1000">
      <div class="questionItem">
        <div class="question-title flex-row">
          <div class="text">{{detail.title}}</div>
        </div>
        <div class="content">
          <div class="contentInner" :class="{active: $route.params.tab === 2 && $route.params.index === 1}">
            <div v-for="(question, no) in detail.answers" :key="no" v-html="question"></div>
          </div>
        </div>
      </div>
    </div>
    <div v-else>
      <div class="intro">
        <div class="question-title flex-row">
          <div class="text">掘金宝资讯项目是什么？</div>
        </div>
        <div class="intro-content">
          <div class="intro-item">
            <!-- <div class="intro-title">项目介绍</div> -->
            <div class="intro-text"><div class="tag f32">在掘金宝平台上，读文章、看电影，可免费获得掘金宝（原始股），原始股可在180天上市交易时变现，人人有机会赚车赚房。</div></div>
          </div>
          <div class="intro-item">
            <div class="intro-title">掘金宝平台为用户提供以下内容：</div>
            <div class="intro-text"><div class="tag">【价值资讯】</div> 掘金宝平台呕心逐一精选优质媒体号，为用户提供价值资讯，大幅提高信息获取效率，杜绝垃圾资讯；</div>
            <div class="intro-text"><div class="tag">【最全片库】</div> 平台拥有百余个影视片库，共覆盖了1930~2019年共计100万部影片，日观影次数达到几十万次。在平台上，院线最新、全球热门一站播放，掘金宝独家解析全网VIP会员影视，看VIP无需花钱！</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
</template>
<script>
import HeaderNav from '@/components/task/header-nav'
export default {
  components: {
    HeaderNav
  },
  data () {
    return {
      questionList: [
        {
          name: '赚钱提现',
          list: [{
            title: '赚到的掘金宝可以提现吗？什么时候可以提现？',
            answers: [
              '平台计划在上线180天后，在交易所开通交易，直接变现赚到的掘金宝（原始股）。'
            ],
            active: true
          }, {
            title: '掘金宝上市交易是什么意思，怎么上市交易？',
            answers: [
              `上市交易是指，平台将在交易所里为每个持有掘金宝（原始股）的用户开通账户，并将用户的掘金宝（原始股）打到交易所账户里，用户就可以如同买卖股票一样进行交易。`,
              `有些人对上市有怀疑。是因为他们知识有限，对资本市场理解太狭隘。`,
              `除了国内交易市场上市外，其他国家资本市场是不是也可以上市？亚洲多少交易市场、欧美多少交易市场？现在还衍生了区块链市场，都可以选择上市。`,
              `但是交易上市后所面临的问题是一样的，都必须有足够的价值支撑资本机构去炒作。百度、阿里巴巴、新浪等在内的多家上市互联网巨头企业，他们上市企业的注册地都并不在国内（位于加勒比海岛上），上市也都是在国外上市。最近的趣头条、拼多多也在两三年内在美国上市了，这些上市的事实，是质疑否认就能改变的吗。`,
              `以自己狭隘的知识去不断质疑的用户，只会错过这个的机会。`
            ]
          }, {
            title: '1个掘金宝当前值多少钱，以后如何升值？',
            answers: [
              `互联网项目在资本市场的估值是按活跃用户数进行计算的，活跃用户数越多，项目估值越高。`,
              `<span class="redColor">1个掘金宝 = 1股原始股，价值1元（该定价是基于目前成熟的资讯类平台用户的价值最底限100美元（ 即：690元/用户）计算）。</span>`,
              `随着平台活跃用户数增长，掘金宝将不断升值，预计上市时，1个掘金宝将达到1美金。`,
              '<span class="redColor">平台当前拥有【价值资讯】和【最全影视片库】两大功能，热门新闻、最新大片天天更新，源源不断吸引新用户观看活跃——平台活跃用户越多，掘金宝（原始股）升值越快，原始股升值越快，能进一步吸引更多用户分享和参与，正向循环，让掘金宝不断升值。</span>',
              '掘金宝奖励并非无限发放的，上市前掘金宝发行总量=用户总数*100个掘金宝。',
              '用户获得的奖励每2个月减半一次，越早参与的用户越能赚得更多奖励；平台将不断回购销毁已发行的掘金宝，让掘金宝数量不断减少，使掘金宝价值得到维稳。',
              '平台将不断新增创新型产品和功能，如行家自媒体，生意自媒体，金融自媒体，并寻求政府部门合作，让平台价值不断攀升、掘金宝更值钱。',
              '<span class="redColor">掘金宝资讯作为一个成长性极高的新平台，按成熟类平台用户估值，本身估值偏低；倘若上市后考虑未来预期，掘金宝价格会上10美元以上。</span>'
            ]
          }, {
            title: '谁给我发这个钱，是你们平台吗？你们平台靠什么盈利?',
            answers: [
              '天上并不会掉钱，钱是通过价值创造，资本市场给予的。掘金宝资讯活跃用户若突破1000万，资本市场估值将不低于10亿美金，这部分资金将由持有掘金宝的用户瓜分。',
              '原始股升值千百倍的案例很多，如下面例子：',
              '<span class="redColor">海尔电器：当年1元原始股，现在变成了367元；</span>',
              '<span class="redColor">贵州茅台：当年1元原始股，现在变成1095元；</span>',
              '<span class="redColor">格力电器:当年1元原始股，现在变成1651元；</span>',
              '<span class="redColor">百度:当年1元原始股，现在变成1780元；</span>',
              '<span class="redColor">腾讯:当年1元原始股，现在变成14400元。</span>',
              '<span class="redColor">阿里巴巴:当年1元原始股，现在变成161422元。</span>',
              '<span class="redColor">所以，掘金宝上市后，1掘金宝不止1块钱，至少价值1美金；</span>',
              '当活跃用户达到千万级，1个掘金宝就是10美金！',
              '平台的早期收入来自于接入的广告，用户越多广告收入越大，扣除运营推广费用后，全部用于回购注销掘金宝（原始股）。'
            ]
          }, {
            title: '哪家平台会有这么好事情，我不相信天上会掉馅饼。',
            answers: [
              '天上本来就不会有馅饼掉下来，互联网历史上从来没有与用户分享平台价值的先例。',
              '互联网发展至今，红利殆尽，各领域均已成熟，掘金宝创业团队为了从创业红海中杀出来，第一家创造了这个和用户分享平台资本市场价值的模式。',
              '当然，用户的奖励均来自于用户的活跃及分享传播，没有贡献就没有奖励。'
            ]
          }, {
            title: '这个项目变现时间太长，来钱太慢。',
            answers: [
              '<span class="redColor">每天仅需打开掘金宝看最新资讯、热门电影，一键分享微信群或者朋友圈，就能立刻锁粉赚取掘金宝，就这么简单！</span>',
              '持有的掘金宝仅需180天就能交易变现（若平台用户达到千万，直接开通交易所变现），以后还能不断升值。只有跑在前面、赚掘金宝多的人，才有机会赚车子赚房子。',
              '平台奖励规则很公平、门槛低，人人都可参与，就看谁攒的原始股多，拿的时间长。'
            ]
          }]
        },
        {
          name: '分享锁粉',
          list: [{
            title: '我邀请了新用户是不是都算数？',
            answers: [
              '只有活跃用户才被资本市场认可，死粉、机器粉是没价值的，平台会定期清理死粉、机器粉，请保证所邀请的用户真实、有效。'
            ]
          }, {
            title: '朋友圈都是熟人，不好意思分享。',
            answers: [
              `分享掘金宝里任何文章给好友，只要好友点击阅读，立即锁粉，奖励到账！`,
              `没有比这个更快赚掘金宝的方法了。`
            ]
          }, {
            title: '掘金宝是不是真的？',
            answers: [
              `掘金宝没有其他多级分销平台复杂套路，都不要想复杂了！不需要交钱，不需要手动填写验证码，简单分享一下就可以锁粉。`
            ]
          }, {
            title: '我怕浪费时间推广，最后功亏一篑。',
            answers: [
              `平时用户获取资讯都是从垃圾信息海洋里挑选内容，挑选的时间就是极大浪费。`,
              `掘金宝已经过滤掉大多数垃圾资讯，而且在内容筛选上不断优化，提高资讯价值，正式运营后，新的内容机制上线，内容更优质。`,
              `今日头条为了减少垃圾内容，引入人工审核机制，不仅限制平台发展，内容更是参差不齐；趣头条更不用说，低俗内容到处都是。从这个角度，使用掘金宝本身就是为用户节省时间。`,
              `同时平台上影视全网最新、片库最大，为老百姓省了一大笔钱。有人质疑版权问题，请回忆下今日头条当时如何面对版权问题的，现在又如何？随着掘金宝平台不断发展，这些问题都会迎刃而解。掘金宝后期将直接购买所有热映电影版权，真正让影视永久免费。`,
              `不投钱，不伤粉，共同受益，大家省钱，能有什么损失？`
            ]
          }, {
            title: '掘金宝老板是谁，公司在哪里？',
            answers: [
              `用户总要来公司考察，摸老底，总感觉自己会被骗。那么这样的思维还是传统生意的思维，并非互联网传播思维。`,
              `互联网的威力在于用户可以指数级增长，如果一个模式成功的基础是以公司规模、员工数量、老板实力为基础才能建立，那么这些因素都是这个模式的天花板！`,
              `传统行业就是以上述因素为发展基础，需要一个区域一个区域去拓展用户，为了提高生产力不得不建立一号二号生产基地。传统行业的摊子只会铺的越大，公司硬成本也会越来高，这是公司发展构成了很大障碍。`,
              `但是互联网恰恰相反：今日头条、京东、美团，哪个公司市值更大，模式最轻松？答案是今日头条，其他模式就是因为轻易和线下融入，才被地理因素处处受限，不得不加大成本拓展市场。`,
              `最易成功的模式成本最轻，成功率才最高。所以我们平台对此问题一直缄默，不是不让用户前往考察公司，而是介于用户思维固化，如果用户只是被眼前好坏影响，反而降低了用户积极性。`,
              `所以，互联网模式成功不是建立在公司本身规模和个人因素的，而是基于模式逻辑、资金流转和发展空间。掘金宝不从用户身上掏一分钱，前期来源于广告价值，后期和持有原始股用户一样，从资本市场变现，没有跑路风险，团队足够轻，将极大有利于后期资本市场谈判，这才是对用户最大收益。`
            ]
          }, {
            title: '我是团队长，能不能给我讲课？',
            answers: [
              `需要讲课学习，需要老百姓动脑子辨别的模式，都是复杂的模式，一旦陷入这种境地，都必然降低效果，需要花费大量成本去教育用户。`,
              `掘金宝需要讲课吗？不需要，只需要放平心态，看到好文章分享一下，新影视分享一下，这样邀请的用户比什么都活跃有价值。所以如果只宣传赚钱，别人就会怀疑平台是不是骗人的，你的分享动机就暴漏了你的野心，只有真正成为平台活跃用户，你分享时才是最自然的，这样锁定的用户才是活跃用户。`
            ]
          }]
        },
        {
          name: '玩法规则',
          list: [{
            title: '绑定了微信和手机号，信息是否泄密？',
            answers: [
              '微信或手机号，仅仅用于登录验证，证明使用者身份，对平台来说并无价值。另外，微信或手机号在存储时已做严格加密处理，无法泄露。'
            ]
          }, {
            title: '平台提供了哪些赚钱方法，看文章如何赚钱，哪个方式赚钱速度最快？',
            answers: [
              `平台为用户提供了以下赚钱方法（可在任务页面查看）：`,
              `<span class="attention">注：金币可兑换为掘金宝，10000金币 *汇率 =1掘金宝；所有奖励每2个月减半一次，下方列举为未减半时奖励</span>`,
              `<span class="tip-title">1）首次邀请好友奖励&nbsp;<span class="redColor">100掘金宝/首次</span></span>`,
              `通过分享文章、海报、邀请链接邀请新用户，首次可获得100掘金宝；`,
              `<span class="tip-title">2）邀请好友奖励  <span class="redColor">50掘金宝（不限次数，锁粉最快，奖励最高）</span></span>`,
              `通过分享文章、海报、邀请链接邀请新用户，再次可获得50掘金宝；`,
              `<span class="tip-title">3）获得新用户大礼包  <span class="redColor">10掘金宝/首次</span></span>`,
              `点击首页领取大礼包领取按钮，领取大礼包，可获得10掘金宝；`,
              `<span class="tip-title">4）时段奖励 100金币/30分钟</span>`,
              `每30分钟，可在首页右上角领取100金币；`,
              `<span class="tip-title">5）邀请的好友每日阅读提成奖励    每日1次</span>`,
              `每日24:00，用户阅读奖励提成 = 邀请好友当日阅读奖励 * 2倍；`,
              `<span class="tip-title">6）填邀请码奖励  10掘金宝/首次</span>`,
              `填写邀请码，经过检测通过，成功绑定，填邀请码可获得10掘金宝奖励；`,
              `<span class="tip-title">7）问券调查奖励 500金币/首次</span>`,
              `认真填写并提交问卷，自动发放500金币奖励；`,
              `<span class="tip-title">8）开宝箱分享奖励 450金币/4小时</span>`,
              `每4小时可开一次宝箱，奖励150金币，分享宝箱，再得300金币；`,
              `<span class="tip-title">9）阅读文章奖励  最高10000金币/天</span>`,
              `每日认真阅读文章，有机会获得100金币，每天最高奖励100篇；`,
              `注册前7天的新用户，通过APP阅读文章，每天可额外获得30%奖励；`,
              `<span class="tip-title">10）每日阅读时长达60分钟 1500金币/天</span>`,
              `每日累计阅读60分钟，自动获得1500奖励；`,
              `<span class="tip-title">11）分享文章奖励  最高600金币/天</span>`,
              `阅读文章并进行分享，每次200金币，每天可分享3次；`,
              `<span class="tip-title">12）晒收入奖励 500金币/天</span>`,
              `在钱包页面，点击晒收入并分享成功，每天首次可获得500金币；`,
              `<span class="tip-title">13）每日签到奖励  （奖励跟随任务页面变化）</span>`,
              `每日首次进入“任务”页面，自动发放；`,
              `<span class="tip-title">14）阅读赚车赚房页面奖励 300金币/首次</span>`,
              `通过任务，点击阅读赚车赚房页面，认真阅读可得300金币；`,
              `<span class="tip-title">15）阅读推送文章奖励  100金币/篇</span>`,
              `阅读服务号或APP推送的文章，可得100金币；`,
              `<span class="tip-title">16）绑定手机号奖励  1000金币/首次</span>`,
              `绑定手机号可得1000金币`,
              `<span class="tip-title">17）绑定微信奖励  1000金币/首次</span>`,
              `绑定微信可得1000金币`
            ]
          }]
        }
      ],
      detail: {}
    }
  },
  mounted () {
    if (this.$route.params.tab < 1000) {
      this.detail = this.questionList[this.$route.params.tab].list[this.$route.params.index]
      document.title = this.detail.title
    } else {
      document.title = '掘金宝资讯项目是什么？'
    }
  }
}
</script>

<style lang="scss">
#questionDetail {
  height: 100vh;
  box-sizing: border-box;
  overflow: auto;
   &.Router {
    transition: all 0s ease;
  }
  .intro {
    padding: 25px 0;
    .question-title {
      padding: 15px 30px 0;
      .text {
        font-size: 34px;
      }
    }
    .tag {
      font-size: 28px;
      &.f30 {
        font-size: 30px;
      }
    }
    .intro-content {
      border-radius: 15px;
      margin: 0 auto;
      padding: 10px 30px 20px;
      box-sizing: border-box;
      .intro-item {
        padding: 15px 0;
        &:last-child{
          border-width: 0;
        }
        .intro-title {
          padding-bottom: 20px;
          font-size: 32px;
          color: #333333;
          font-weight: 900;
          line-height: 45px;
        }
        .intro-text {
          .tag {
            color: #BE9251;
          }
          font-size: 30px;
          color: #333333;
          line-height: 50px;
          margin-bottom: 15px;
          &:last-child {
            margin-bottom: 0;
          }
        }
      }
    }
  }
  .question-title {
    padding: 40px 30px 30px;
    line-height: 42px;
    font-size: 32px;
    justify-content: space-between;
    .text {
      text-align: justify;
      flex: 1;
      font-size: 32px;
      color: #333;
      font-weight: 900;
    }
    .arrowEl {
      border-color: #B4B4B4;
      width: 16px;
      height: 16px;
      transition: transform 400ms;
      &.active {
        transform: rotateZ(225deg);
      }
    }
  }
  .content {
    overflow: hidden;
    transition: height 400ms;
    .contentInner {
      padding: 0 30px 25px;
      font-size: 32px;
      color: #666666;
      line-height: 56px;
      > div {
        margin-bottom: 20px;
        &:last-child {
          margin-bottom: 0;
        }
      }
      .redColor {
        color: #BE9251;
      }
      &.active {
        > div {
          margin-bottom: 0
        }
      }
    }
  }
  .tip-title {
    font-weight: 900;
    margin-top: 15px;
    margin-bottom: 10px;
    display: inline-block;
  }
}
</style>



